<script setup lang="ts">
    import useFetch from "@/composables/useFetch";
    import { ref } from "vue";

    type Grevfx = {
        grEvfxNm: string;
        grEvfxCd: string;
    };

    const emit = defineEmits<{
        fetchList: [p: CloseRangeResDto];
    }>();

    const elitEvfxYn = ref<"Y" | "N">("N");

    const grevfxArr = ref<Grevfx[]>([]);
    const chosenGrevfx = ref<Grevfx | null>(null);

    const jongmokArr = ref<OnMainEvfxResList[]>([]);
    const chosenjongmok = ref<OnMainEvfxResList[]>([]);

    //검색옵션

    const sidoOps = ref<OpsList[]>([]);
    const sido = ref<string>("");
    const clubNm = ref<string>("");

    const etcOps = ref<any>({
        pkgPsbYn: "N",
        wifiOffrYn: "N",
        shtlOprtYn: "N",
        dsbpFaclOffrYn: "N"
    });

    // 운영종목
    useFetch<Grevfx[]>({
        url: "/search/grevfx",
        param: () => ({
            elitEvfxYn: elitEvfxYn.value
        }),
        dependancy: [() => elitEvfxYn.value],
        onSuccess: data => {
            console.log(data);
            grevfxArr.value = [{ grEvfxNm: "전체", grEvfxCd: "*" }, ...data.map(a => ({ ...a }))];
            chosenGrevfx.value = { grEvfxNm: "전체", grEvfxCd: "*" };
        }
    });

    // 종목 선택
    useFetch<MainTabCon02Type>({
        url: () => `/on-main/evfx/{grEvfxCd}?grEvfxCd=${chosenGrevfx.value?.grEvfxCd}`,
        dependancy: [() => chosenGrevfx.value],
        enable: () => !!chosenGrevfx.value,
        onSuccess: data => {
            console.log("AA", data);
            const initValue = { grEvfxNm: "전체", grEvfxCd: "", mchEvfxCd: null, mchEvfxNm: "GX프로그램" };
            jongmokArr.value = [{ ...initValue }, ...data.onMainEvfxResList];
            chosenjongmok.value = [{ ...initValue }];
        }
    });
    // 운영종목 시도코드
    useFetch<SidoList[]>({
        url: "/search/grevfx-sido",
        onSuccess: data => {
            console.log(data);
            sidoOps.value = [
                { label: "시도 전체", value: "" },
                ...data.map(a => ({ value: a.siDoCd, label: a.siDoNm }))
            ];
        }
    });
    const { fetchData } = useFetch<any>({
        url: "/search/grevfx-sportclub",
        param: () => ({
            elitEvfxYn: elitEvfxYn.value,
            sido: sido.value,
            clubNm: clubNm.value,
            grEvfxCd: chosenjongmok.value.map(a => a.grEvfxCd),
            ...etcOps.value
        }),
        dependancy: [() => chosenGrevfx.value, () => chosenjongmok.value],
        enable: () => !!chosenGrevfx.value && !!chosenjongmok.value.length,
        onSuccess: data => {
            console.log("clubs", data);
            emit("fetchList", data);
        }
    });
</script>

<template>
    <div class="tab-wrap" :style="{ 'border-bottom': '1px solid #d6dce5', 'margin-bottom': '61px' }">
        <ul class="tab bottom">
            <li :class="{ 'is-active': elitEvfxYn === 'N' }" @click="elitEvfxYn = 'N'">일반종목</li>
            <li :class="{ 'is-active': elitEvfxYn === 'Y' }" @click="elitEvfxYn = 'Y'">선수반종목</li>
        </ul>
    </div>
    <div class="inner-w">
        <h3 class="title-sub mb-18">운영종목<i class="required"></i></h3>
        <ul class="tab square d-flex" :style="{ 'margin-bottom': '60px' }">
            <li
                class="opsBtn"
                v-for="grevfx in grevfxArr"
                :key="grevfx.grEvfxCd"
                :class="{ 'is-active': chosenGrevfx?.grEvfxCd == grevfx.grEvfxCd }"
                @click="chosenGrevfx = grevfx"
            >
                {{ grevfx.grEvfxNm }}
            </li>
        </ul>
        <h3 class="title-sub mb-18">종목 선택<i class="required"></i></h3>
        <ul class="tab square jongmokWrap mini" :style="{ 'margin-bottom': '80px' }">
            <li
                class="opsBtn"
                v-for="jongmok in jongmokArr"
                :key="jongmok.grEvfxCd"
                :class="{ 'is-active': chosenjongmok.find(a => a.grEvfxCd === jongmok.grEvfxCd) }"
                @click="
                    () => {
                        if (jongmok.grEvfxNm === '전체') {
                            if (chosenjongmok.length && chosenjongmok.find(a => a.grEvfxNm === '전체')) {
                                chosenjongmok = [];
                            } else {
                                chosenjongmok = [{ ...jongmok }];
                            }
                            return;
                        }

                        const target = chosenjongmok.find(a => a.grEvfxCd === jongmok.grEvfxCd);
                        if (target) {
                            chosenjongmok = chosenjongmok.filter(a => a.grEvfxCd !== jongmok.grEvfxCd);
                        } else {
                            if (chosenjongmok.find(a => a.grEvfxNm === '전체')) {
                                chosenjongmok = [{ ...jongmok }];
                            } else {
                                chosenjongmok = [...chosenjongmok.map(a => ({ ...a })), { ...jongmok }];
                            }
                        }
                    }
                "
            >
                {{ jongmok.grEvfxNm }}
            </li>
        </ul>
        <div class="searchOpsBox d-flex ai-center jc-between">
            <div class="d-flex">
                <select class="jongmokSelect mr-10" v-model="sido">
                    <option :value="op.value" v-for="op in sidoOps">{{ op.label }}</option>
                </select>
                <input class="keywordInput" v-model="clubNm" placeholder="클럽명을 입력해주세요." />
            </div>
            <div class="etcOps">
                <span class="tit mr-40">부가시설</span>

                <input type="checkbox" id="a" v-model="etcOps.pkgPsbYn" true-value="Y" false-value="N" />
                <label class="etcCheckbox" for="a">주차장</label>
                <input type="checkbox" id="b" v-model="etcOps.wifiOffrYn" true-value="Y" false-value="N" />
                <label class="etcCheckbox" for="b"> 무선인터넷 </label>
                <input type="checkbox" id="c" v-model="etcOps.shtlOprtYn" true-value="Y" false-value="N" />
                <label class="etcCheckbox" for="c"> 셔틀버스 </label>
                <input type="checkbox" id="d" v-model="etcOps.dsbpFaclOffrYn" true-value="Y" false-value="N" />
                <label class="etcCheckbox" for="d"> 장애인시설물 </label>
            </div>
            <button class="searchBtn" @click="fetchData">검색</button>
        </div>
    </div>
</template>

<style scoped lang="scss">
    .title-sub {
        @include font-style(22);
        font-weight: 500;
    }

    .tab {
        display: flex;
        flex-wrap: wrap;

        &.bottom {
            position: relative;
            height: 66px;
            max-width: 1380px;
            margin: 0 auto;

            li {
                font-size: 18px;
                width: 50%;
                color: #555;
                cursor: pointer;
                &.is-active {
                    position: relative;
                    &::after {
                        content: "";
                        display: block;
                        width: 100%;
                        height: 3px;
                        background: #111;
                        position: absolute;
                        bottom: 0;
                    }
                    font-weight: 500;
                    color: #333;
                }
            }
        }

        li {
            @include flex;
        }

        &.square {
            /* height: 54px; */
            gap: 12px;
            li {
                border-radius: 14px;
                height: 56px;
                line-height: 56px;
                width: calc(100% / 6 - 10px);
                &.is-active {
                    color: #0fae6f;
                    border-color: #0fae6f;
                    font-weight: 500;
                    background: rgba(15, 174, 111, 0.05);
                    box-shadow: 4px 4px 18px 0 rgba(0, 0, 0, 0.15);
                }
            }

            &.mini {
                height: auto;
                gap: 9px;
                flex-wrap: wrap;
                justify-content: flex-start;

                li {
                    width: calc((100% / 10) - 8.1px);
                    height: 46px;
                    border-radius: 12px;

                    &.is-active {
                        color: #fff;
                        font-weight: 500;
                        border-color: #0fae6f;
                        background: #0fae6f;
                        box-shadow: 4px 4px 12px 0 rgba(0, 0, 0, 0.17);
                    }
                }
            }
        }
    }
    .required {
        display: inline-block;
        width: 8px;
        height: 8px;
        background: #0fae6f;
        margin-left: 13px;
        border-radius: 50%;
    }

    .searchOpsBox {
        %uiStyle {
            border-radius: 8px;
            border: solid 1px #d6dce5;
            background-color: #fff;
            height: 42px;
            color: #999999;
            @include font-style(15);
            padding: 0 16px;
        }
        background-color: #f9f9f9;
        border: solid 1px #e8eff4;
        border-radius: 12px;
        padding: 34px 60px;
        margin-bottom: 80px;
        select {
            width: 140px;
            @extend %uiStyle;
            cursor: pointer;
            background: url("@/assets/icon/ic_arrow.svg") no-repeat right 16px center #fff;
        }
        .keywordInput {
            @extend %uiStyle;
            width: 280px;
        }
        .etcOps {
            .tit {
                @include font-style(16);
                font-weight: 500;
            }
            .etcCheckbox {
                color: #555555;
                @include font-style(16);
                margin-right: 30px;
                cursor: pointer;
                &:last-child {
                    margin-right: 0;
                }
                &::before {
                    content: "";
                    display: inline-block;
                    width: 20px;
                    height: 20px;
                    border-radius: 4px;
                    border: solid 1px #d6dce5;
                    background-color: #fff;
                    box-sizing: border-box;
                    position: relative;
                    top: 4px;
                    margin-right: 5px;
                }
            }
            input {
                display: none;
                &:checked + label::before {
                    border: none;
                    background: url("@/assets/imgs/selectbox_active.svg") no-repeat center center #0fae6f;
                }
            }
        }
        .searchBtn {
            color: #0fae6f;
            height: 42px;
            text-align: center;
            line-height: 42px;
            border-radius: 8px;
            background-color: #fff;
            @include font-style(15);
            font-weight: 500;
            border: solid 1px #0fae6f;
            width: 90px;
        }
    }
</style>
